.App {
  background-color: #eee;
  position: relative;
  overflow: hidden;
}

#bg-sq1 {
  height: 1550px;
  width: 900px;
  background: linear-gradient(#2f1d3a, #543169);
  position: absolute;
  margin-top: 9rem;
  z-index: 0;
  transform: rotate(6deg);
  margin-left: -9rem;
  border-radius: 1rem;
}

#scroller {
  display: none;
}

ul#scroller li:active a {
  font-size: 1.8rem !important;
  transition: 0.5s;
}

.parallax {
  /* background-image: url("images/bg.jpg"); */

  height: auto;
  width: 100%;

  background-attachment: fixed;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.elem {
  margin: 0 0;
  padding-bottom: 5rem;
  position: relative;
}

.social-buttons {
  text-align: center;
  padding: 6rem 1rem 3rem 1rem;
}

.social-btn {
  padding: 0 3rem;
  display: inline-block;
}

#about-me {
  position: relative;
}

.about-me-wrapper {
  display: grid;
  gap: 4rem;
  grid-template-columns: 1fr;
  grid-auto-rows: minmax(300px, auto);
}

.technical-skills {
  padding: 1.5rem;
  width: 100%;
  height: 100%;
  background: linear-gradient(#f1f1f1, #eef9ff);
  border-radius: 1rem;
  margin-top: 0;
  box-shadow: 0 8px 16px -8px #2a0c3d;
  z-index: 10;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  align-items: center;
  grid-auto-rows: minmax(100px, auto);
}

.technical-skills div .icons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
}

.technical-skills div .icons div {
  justify-items: center;
  display: grid;
}

.technical-skills div .icons div img {
  height: 48px;
  width: 48px;
}

.frontEnd {
  border-bottom: 2px dashed #bbb;
  border-right: none;
}

.technical-skills div h4 {
  text-align: center;
  margin-bottom: 2rem;
}

.technical-skills div div div h5 {
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  padding-bottom: 0.4rem;
  color: rgb(49, 49, 49);
}

.technical-skills div .icons div {
  margin: 0 1rem 2rem 1rem;
}

#htmlcss {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0;
  padding: 0;
}

.sm-txt {
  font-size: 1.2rem;
}

#experience {
  position: relative;
}

.skill-head {
  text-align: center;
  padding-top: 2rem;
  letter-spacing: 0.25rem;
  color: rgb(99, 99, 99);
}

.list-elems {
  line-height: 3rem;

  padding-top: 2rem;
  color: rgb(28, 48, 48);
  text-align: center;
}

.fa-list-elems li {
  padding-bottom: 2rem;
  line-height: 1.5rem;
  color: rgb(18, 38, 38);
  text-align: center;
}

#lang-list-elem li {
  color: #707070;
}

#lang-list-elem li:hover {
  color: #ff396a;
  transition: 0.8s;
}

.hide {
  opacity: 0;
  animation: fadeOut 0.4s ease-out 1;
  display: none;
}

.show {
  visibility: visible;
  animation-name: fadeIn;
  animation-iteration-count: 1;
  animation-timing-function: ease-in;
  animation-duration: 0.4s;
  opacity: 1;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

.exp-list li {
  padding-bottom: 1.25rem;
}

.footer {
  background-color: #2f1d3a;
  margin-bottom: -10px;
  padding-bottom: -20px;
  margin: 0 auto;
  justify-items: center;
  align-items: center;
  text-align: center;
  min-height: 300px;
}

#footer-eml {
  color: white;
  letter-spacing: 0.22rem;
  padding: 5rem 0 3.5rem 0;
}

.resume-btn-wrap {
  border-radius: 12px;
  max-width: 250px;
  padding: 1rem;
  position: relative;
  background: linear-gradient(to bottom right, red, rgb(111, 0, 255));
  padding: 3px;
  margin-bottom: 15rem;
  margin: 0 auto;
}

.resume-btn {
  font-weight: 500;
  letter-spacing: 0.08rem;
  background-color: #2f1d3a;
  border-radius: 12px;
  color: white;
  padding: 1rem 2rem;
  white-space: nowrap;
}

.resume-btn-link:hover,
.resume-btn-wrap:hover {
  text-decoration: none;
  cursor: pointer;
  background: linear-gradient(to top left, red, rgb(111, 0, 255));
}

/* MEDIA QUERIES */

@media only screen and (min-width: 767px) {
