  }

  #about-me-p {
    line-height: 2.4rem;
    letter-spacing: 0.08rem;
    margin-top: 2.8rem;
    color: #eee;
  }

  .about-me-wrapper {
    display: grid;
    gap: 4rem;
    grid-template-columns: 40% 60%;
    grid-auto-rows: minmax(200px, auto);
  }

  .technical-skills {
    margin-top: 2.8rem;
